import { useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import useAuth from '../../../hooks/auth/useAuth';
import Arrow from '../../Icons/Arrow/Arrow';
import Circles from '../../Icons/Circles/Circles';
import Cloud from '../../Icons/Cloud/Cloud';
import Cog from '../../Icons/Cog/Cog';
import Eye from '../../Icons/Eye/Eye';
import Grid from '../../Icons/Grid/Grid';
import HardDrive from '../../Icons/HardDrive/HardDrive';
import Heart from '../../Icons/Heart/Heart';
import Logo from '../../Icons/Logo/Logo';
import Moon from '../../Icons/Moon/Moon';
import Person from '../../Icons/Person/Person';
import Persons from '../../Icons/Persons/Persons';
import Quote from '../../Icons/Quote/Quote';
import Scissors from '../../Icons/Scissors/Scissors';
import DarkModeToggle from '../DarkModeToggle/DarkModeToggle';

const Sidenav = () => {
  const { isAuthenticated, logout } = useAuth();
  const isDesktop = document.documentElement.clientWidth >= 640;
  const [isOpen, setIsOpen] = useState(isDesktop);

  const navItemClassName = `my-1 py-2 px-2 flex`;
  const navItemActiveClassname = `bg-slate-200 dark:bg-slate-700`;
  const iconSize = 'h-6 w-6';
  const iconClassName = `${iconSize} text-gray-600 dark:text-gray-400 flex-shrink-0`;
  const sectionTitleClassName = `mt-5 border-b-2 border-gray-200 text-lg font-semibold dark:text-white hidden ${
    !isOpen ? 'pl-2' : ''
  } ${navItemClassName}`;

  return (
    <aside
      className={`body-font fixed top-0 left-0 right-0 bottom-0 z-50 min-h-screen transition-transform duration-300 sm:static sm:transition-all ${
        isOpen
          ? 'w-full translate-x-0 sm:w-1/5 sm:min-w-[15rem]'
          : 'w-full translate-x-[-97%] sm:w-[4.3rem] sm:min-w-0 sm:translate-x-0'
      }`}
      onClick={() => !isDesktop && isOpen && setIsOpen(false)}
    >
      <div
        className={`${
          isOpen ? 'overflow-y-auto sm:overflow-visible' : ''
        } static top-0 h-full border-r-2 border-gray-100 bg-slate-50 dark:border-gray-700 dark:bg-slate-800 sm:sticky sm:h-auto sm:whitespace-nowrap`}
      >
        <button
          className={`absolute sm:right-[-1rem] ${
            isOpen ? 'right-2' : '-right-4'
          } top-10 z-30 border-2 bg-white p-1 dark:border-gray-700 dark:bg-black dark:text-white`}
          onClick={() => setIsOpen(!isOpen)}
        >
          <Arrow className={`h-3 w-3 ${isOpen ? 'rotate-180' : ''}`} />
        </button>
        <div className="flex min-h-screen flex-col overflow-hidden py-5 px-3">
          <Link className={`title-font -mx-1 ${navItemClassName}`} to="/owner">
            <Logo className="h-8 w-8 shrink-0" />
            <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Owner</span>
          </Link>

          <div className="py-5">
            <span className={`${sectionTitleClassName}`}>
              Y<span className={`${!isOpen && 'hidden'}`}>ou</span>
            </span>
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/profile"
            >
              <Heart className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>My Profiles</span>
            </NavLink>
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/connections"
            >
              <Persons className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Connections</span>
            </NavLink>
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/circles"
            >
              <Circles className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Circles</span>
            </NavLink>
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/apps"
            >
              <Grid className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Apps</span>
            </NavLink>
          </div>

          <div className="py-5">
            <span className={`${sectionTitleClassName}`}>
              A<span className={`${!isOpen && 'hidden'}`}>pps</span>
            </span>
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/website"
            >
              <Cloud className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Website</span>
            </NavLink>
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/blog"
            >
              <Quote className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Blog</span>
            </NavLink>
          </div>

          <div className="py-5">
            <span className={`${sectionTitleClassName}`}>
              S<span className={`${!isOpen && 'hidden'}`}>ystem</span>
            </span>
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/drives"
            >
              <HardDrive className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Drives</span>
            </NavLink>
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/settings"
            >
              <Cog className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Settings</span>
            </NavLink>
          </div>

          <div className="mt-auto py-10">
            <span className="flex px-2">
              <Moon className={`my-auto ${iconClassName} ${!isOpen && 'hidden'}`} />
              <span className={`my-auto mx-3 ${!isOpen && 'hidden'}`}>Dark mode</span>
              <DarkModeToggle className={!isOpen ? 'ml-[-0.8rem]' : ''} />
            </span>
            {isAuthenticated ? (
              <button onClick={() => logout()} className={`w-full ${navItemClassName}`}>
                <Person className={`${iconClassName}`} />
                <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Log out</span>
              </button>
            ) : null}
            <NavLink
              className={({ isActive }) =>
                `${navItemClassName} ${isActive && navItemActiveClassname}`
              }
              to="/owner/demo-data"
            >
              <Scissors className={`${iconClassName}`} />
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>Demo Data</span>
            </NavLink>
          </div>
          <div>
            <p className={`${navItemClassName} opacity-40`}>
              <span className={`${iconSize} text-center text-2xl`}></span>{' '}
              <span className={`my-auto ml-3 ${!isOpen && 'hidden'}`}>
                2022 | Youfoundation <br />
                v.
                {typeof process.env.REACT_APP_VERSION !== 'number'
                  ? process.env.REACT_APP_VERSION
                  : new Date(process.env.REACT_APP_VERSION).toLocaleString()}
              </span>
            </p>
          </div>
        </div>
      </div>
    </aside>
  );
};

export default Sidenav;
