import { DataUtil, TargetDrive, ThumbSize } from '@youfoundation/transit-lib';

export class ContactConfig {
  static readonly ContactDriveType: string = DataUtil.toByteArrayId('contact_drive_type');
  static readonly ContactFileType: number = 100;

  static readonly ContactTargetDrive: TargetDrive = {
    alias: DataUtil.toByteArrayId('contact_drive'),
    type: ContactConfig.ContactDriveType,
  };
}

export interface ContactDataImage extends ThumbSize {
  content: string;
}

export interface Contact {
  name?: { givenName: string; surname: string };
  image?: ContactDataImage;
  dotYouId?: string;
  id?: string;
  fileId?: string;
}
